<template>
  <tas-base-crud ref="sub-module" :config="config" hide-create hide-delete hide-update hide-filter hide-detail>
    <template v-slot:list-table-action="data">
      <a @click="openModal('modal-rincian-komponen', data)" class="btn btn-info btn-sm">Konfigurasi Komponen</a>
    </template>
    <template v-slot:list-action>
      <b-modal v-if="modalData && modalData.detail" id="modal-rincian-komponen" hide-footer centered size="xl" :title="'Konfigurasi Komponen - '+modalData.detail.name">
        <div class="d-flex justify-content-between align-items-start">
          <div class="">
            <table class="table no-border mb-0">
              <tr>
                <td class="pt-0 pl-0 nowrap-table">Kategori Komponen</td>
                <td class="pt-0 pr-0 nowrap-table">: {{ modalData.detail.rel_salary_component_category_id }}</td>
              </tr>
              <tr>
                <td class="pt-0 pl-0 nowrap-table">Jabatan</td>
                <td class="pt-0 pr-0 nowrap-table">: {{ modalData.detail.rel_job_position_category_id }} - {{ modalData.detail.rel_status_employment_id }}</td>
              </tr>
            </table>
          </div>
          <div class="form-group mb-0">
            <label>Status Komponen</label>
            <div class="radio-inline">
              <label class="radio justify-content-start">
                <input @change="updateStatus()" v-model="modalData.detail.active" type="radio" :value="1" :true-value="1" :false-value="0">
                <span class="mr-2"></span> Komponen Aktif </label>
              <label class="radio justify-content-start ml-5">
                <input @change="updateStatus()" v-model="modalData.detail.active" type="radio" :value="0" :true-value="1" :false-value="0">
                <span class="mr-2"></span> Komponen Nonaktif </label>
            </div>
          </div>
        </div>
        <hr>
        <b-overlay :show="!modalData.detail.active" rounded="sm">
          <template #overlay>
            <div class="text-center">
              <i class="ri-lock-2-fill ri-3x"></i>
              <h2 class="text-muted">Aktifkan komponen untuk mengatur proporsi akun keuangan (FINA)</h2>
            </div>
          </template>
          <div class="d-flex mb-5">
            <div class="mb-0">
              <h5 class="mb-0">Kode Akun (FINA)</h5>
              <span class="text-muted font-size-sm">Pilih kode akun dan pastikan Total Persentase mencapai 100%</span>
            </div>
            <div class="d-flex flex-fill">
              <input v-model.lazy="listChecklist.search" type="text" name="search" class="form-control ml-5" placeholder="Cari Nama / Kode Akun">
            </div>
          </div>
          <template v-if="listChecklist.data.length">
            <b-overlay :show="isLoadingChecklist" :opacity=".90">
              <table class="table table-head-custom table-vertical-center table-head-bg">
                <thead>
                  <tr>
                    <th>Kode Akun</th>
                    <th>Debit/Kredit</th>
                    <th class="text-center max-w-50px">Proporsi</th>
                  </tr>
                </thead>
                <tbody>
                  <tr v-for="(e,i) in listChecklist.data" :key="i+'-data-employee'">
                    <td>
                      <label class="checkbox">
                        <input @change="checkActive($event, e, i)" v-model.number="e.active" type="checkbox" :true-value="1" :false-value="0">
                        <span class="mr-3"></span>
                        <div class="flex-fill"> {{ e.account_name }} <span class="d-block font-size-sm font-weight-bold">Kode Akun: {{ e.account_code }}</span>
                        </div>
                      </label>
                    </td>
                    <td class="text-uppercase nowrap-table text-center"> {{ e.debit_kredit }} </td>
                    <td class="text-center max-w-50px">
                      <input v-if="e.active === 1" v-model.lazy.number="e.proportion_value" @change="submitData(e)" placeholder="Proporsi" type="number" class="form-control">
                      <!-- <s-input v-show="e.active === 1" @input="submitData(e)" v-model.lazy="e.proportion_value" :item="{ type:'number', validation:[], placeholder: 'Proporsi', api:'', nm:true, ic:'w-100 mt-0' }" /> -->
                      <i v-else class="ri-lock-2-fill ri-2x"></i>
                    </td>
                  </tr>
                </tbody>
              </table>
              <div class="d-flex justify-content-between align-items-center">
                <b-pagination class="mb-0" size="md" prev-text="Sebelumnya" next-text="Selanjutnya" hide-goto-end-buttons v-model="listChecklist.page" :total-rows="listChecklist.total" :per-page="listChecklist.limit" />
                <span>Total Proporsi {{ modalData.detail.total_proportion_value || 0 | parse('number') }}%</span>
              </div>
            </b-overlay>
          </template>
        </b-overlay>
      </b-modal>
    </template>
  </tas-base-crud>
</template>

<script>
export default {
  name: 'crud-mapping-salary-accounts-detail',
  data () {
    return {
      modalData: {},
      mainKey: 0,
      isLoadingChecklist: false,
      listChecklist: {
        page: 1,
        search: '',
        filters: {},
        limit: 5,
        total: 0,
        totalPage: 0,
        data: []
      },
      formChecklist: [],
      config: {
        title: 'Komponen Penggajian',
        model_api: null,
        getter: 'custom/mapping-salary-accounts',
        setter: 'custom/mapping-salary-accounts',
        pk_field: 'name',
        filter_api: {},
        custom_api: {},
        permission: {
          create: 'create-mapping-salary-accounts',
          read: 'view-mapping-salary-accounts',
          update: 'update-mapping-salary-accounts',
          delete: 'delete-mapping-salary-accounts'
        },
        slave: [],
        fields: [
          { id: 'id', methods: { list: false, detail: false, create: false, update: false, filter: false } },
          { id: 'updated_by', methods: { list: false, detail: false, create: false, update: false, filter: false } },
          { id: 'created_by', methods: { list: false, detail: false, create: false, update: false, filter: false } },
          { id: 'created_at', label: 'Dibuat', methods: { create: false, update: false, filter: false } },
          { id: 'updated_at', label: 'Diperbarui', methods: { list: false, detail: false, create: false, update: false, filter: false } },
          { id: 'name', label: 'Nama Komponen', methods: { list: true, detail: true, create: true, update: true, filter: false } },
          {
            id: 'salary_component_category_id',
            label: 'Kategori Komponen Gaji',
            methods: {
              list: { view_data: 'rel_salary_component_category_id' },
              detail: { view_data: 'rel_salary_component_category_id' },
              create: false,
              update: false,
              filter: {
                setter: 'master-salary-component-categories',
                getter: 'master-salary-component-categories',
                type: 'lookup-radio',
                option: {
                  list_pointer: {
                    label: 'name', code: 'id', display: ['name']
                  }
                }
              }
            }
          },
          {
            id: 'fina_salary_account_id',
            label: 'Akun Penggajian (FINA)',
            methods: {
              list: { view_data: 'rel_fina_salary_account_id' },
              detail: { view_data: 'rel_fina_salary_account_id' },
              create: {
                setter: 'fina-salary-accounts',
                getter: 'fina-salary-accounts',
                type: 'lookup-radio',
                option: {
                  list_pointer: {
                    label: 'account_name', code: 'id', display: ['account_name']
                  }
                }
              },
              update: {
                setter: 'fina-salary-accounts',
                getter: 'fina-salary-accounts',
                type: 'lookup-radio',
                option: {
                  list_pointer: {
                    label: 'account_name', code: 'id', display: ['account_name']
                  }
                }
              },
              filter: {
                setter: 'fina-salary-accounts',
                getter: 'fina-salary-accounts',
                type: 'lookup-radio',
                option: {
                  list_pointer: {
                    label: 'account_name', code: 'id', display: ['account_name']
                  }
                }
              }
            }
          },
          {
            id: 'active',
            data: 'active',
            label: 'Status',
            placeholder: null,
            methods: {
              list: {
                order: true,
                class: { 0: 'badge badge-danger', 1: 'badge badge-primary' },
                transform: 'active'
              },
              detail: {
                order: true,
                class: { 0: 'badge badge-danger', 1: 'badge badge-primary' },
                transform: 'active'
              },
              create: {
                view_data: 'active',
                type: 'radio',
                value: '1',
                option: {
                  list_pointer: {
                    label: 'label',
                    code: 'code',
                    list: [{ label: 'Aktif', code: '1' }, { label: 'Non Aktif', code: '0' }]
                  }
                }
              },
              update: {
                view_data: 'active',
                type: 'radio',
                value: '1',
                option: {
                  list_pointer: {
                    label: 'label',
                    code: 'code',
                    list: [{ label: 'Aktif', code: '1' }, { label: 'Non Aktif', code: '0' }]
                  }
                }
              },
              filter: {
                view_data: 'active',
                type: 'radio',
                option: {
                  list_pointer: {
                    label: 'label',
                    code: 'code',
                    list: [{ label: 'Aktif', code: '1' }, { label: 'Non Aktif', code: '0' }]
                  }
                }
              }
            }
          }
        ]
      }
    }
  },
  watch: {
    mainKey: {
      immediate: false,
      deep: false,
      handler (e) {
        this.$refs['sub-module'].$children[1].getData()
      }
    },
    'listChecklist.page': {
      deep: false,
      immediate: false,
      handler (e) {
        this.getListAcount(e)
      }
    },
    'listChecklist.search': {
      deep: false,
      immediate: false,
      handler (e) {
        this.getListAcount(1)
      }
    }
  },
  computed: {
    canSubmit () {
      return true
    }
  },
  methods: {
    async openModal (id, data) {
      const detail = {
        detail: await this.getDetail('get', 'custom/mapping-salary-accounts/show', {
          id: data.rowData.id,
          job_position_category_id: data.rowData.job_position_category_id,
          status_employment_id: data.rowData.status_employment_id
        })
      }
      this.modalData = Object.assign({}, detail, data.rowData)
      this.$set(this.listChecklist, 'filters', {
        job_position_category_id: data.rowData.job_position_category_id,
        status_employment_id: data.rowData.status_employment_id,
        salary_component_id: data.rowData.id,
        active: null
      })
      this.getListAcount()
      this.$nextTick().then(e => {
        this.$bvModal.show(id)
      })
    },
    getDetail (method, api, param, obj = null) {
      return this.$_api[method](api, param).then(res => {
        return obj ? res.data[obj] || res.data : res.data
      }).catch((err) => {
        this.$_alert.error(err)
      })
    },
    submitData (e) {
      const api = 'custom/mapping-salary-account-proportions'
      this.$_api.update(api, e).then(res => {
        this.$_alert.success(null, 'Proporsi berhasil diperbarui')
        this.$set(this.modalData, 'detail', res.data)
        this.mainKey++
      }).catch((err) => {
        this.$_alert.error(err)
      })
    },
    updateStatus (e) {
      const api = this.config.setter
      const form = JSON.parse(JSON.stringify(this.modalData.detail))
      this.$_api.update(api, form).then(res => {
        this.$_alert.success(null, 'Status Komponen berhasil diperbarui')
        this.mainKey++
      }).catch((err) => {
        this.$_alert.error(err)
      })
    },
    checkActive (event, data, index) {
      if (data.active === 0 && data.proportion_value > 0) {
        data.proportion_value = 0
        this.submitData(data)
      }
    },
    getListAcount (page = 1) {
      this.isLoadingChecklist = true
      let filter = {
        page: page,
        search: this.listChecklist.search || null,
        limit: this.listChecklist.limit,
        filters: this.listChecklist.filters
      }
      this.$_api.list('custom/mapping-salary-account-proportions', filter).then(res => {
        let newObj = {
          page: filter.page,
          search: filter.search,
          total: res.total,
          filters: this.listChecklist.filters,
          limit: this.listChecklist.limit,
          totalPage: res.totalPage,
          data: res.data
        }
        this.listChecklist = Object.assign({}, newObj)
        this.isLoadingChecklist = false
      }).catch((err) => {
        this.isLoadingChecklist = false
        this.$_alert.error(err)
      })
    }
  }
}
</script>



